.services__container {
    grid-template-columns: repeat(3, 270px);
    justify-content: center;
    column-gap: 1.8rem;
  }
   
  .modal__content {
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: var(--container-color);
    padding: 6rem 0 2rem 2.5rem;
    border-radius: 5%;
    height: 80vh;
  }
  
  .services__icon {
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
  }
  
  .services__title {
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
  }
  
  .services__button {
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
  }
  
  .services__button-icon {
    font-size: 1rem;
    transition: 0.3s;
  }
  
  .services__button:hover 
  .services__button-icon {
    transform: translateX(00.25rem);
  }
  
  .costum__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
  }
  
  .costum__modal-content {
    width: 500px;
    position: relative;
    background-color: var(--container-color);
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;
  }
  
  .costum__modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: var(--title-color);
    cursor: pointer;
  }
  
  .costum__modal-title,
  .costum__modal-description {
    text-align: center;
  }
  
  .costum__modal-title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);
  }
  
  .costum__modal-description {
    font-size: var(--small-font-size);
    padding: 0.35rem;
    margin-bottom: var(--mb-2);
  }
  
  .costum__modal-service {
    row-gap: 0.75rem;
  }
  
  .costum__modal-service {
    display: flex;
    align-items: center;
    column-gap: .5rem;
  }
  
  .costum__modal-icon {
    color: var(--title-color);
    font-size: 1.1rem;
  }
  
  .costum__modal-info {
    font-size: var(--small-font-size);
  }
  
  /* Active Modal */
  .active-modal {
    opacity: 1  ;
    visibility: visible;
  }
  
    /* Large Devices */
  @media screen and (max-width: 992px) {
    .services__container {
      grid-template-columns: repeat(3, 218px);
    }
  }
  
    /* Medium Devices */
  @media screen and (max-width: 768px) {
    .services__container {
      grid-template-columns: repeat(2, 1fr);
    }
  
    .modal__content {
      padding: 3.5rem 0.5rem 1.25rem 1.5rem;
    }
  
    .services__icon {
      font-size: 1.5rem;
    }
  }
  
  @media screen and (max-width: 576px) {
    .costum__modal-content {
      padding: 4.5rem 1.5rem 2.5rem;
    }
  
    .costum__modal-description {
      padding: 0;
    }
  }
  
    /*Small Devices */
  @media screen and (max-width: 350px) {
    .services__container {
      grid-template-columns: max-content;
    }
  
    .modal__content {
      padding-right: 6rem;
    }
  }